import React from 'react';
import { useSelector } from 'react-redux';
import logo from './logo.svg';
import './App.scss';
import toHex from './ColorRGB';
import toRGB, { toComplementary, toHSV } from './ColorHEX';
import toRGB2 from './ColorHSV';
import { RootState } from './reducks/store/store';

function App() {
    const tmp = useSelector((state: RootState) => state.colors);
    const a = toHex(211, 59, 105);
    console.log(a);

    const b = toRGB('d33b69');
    console.log(b);

    const c = toComplementary('d33b69');
    console.log(c);

    const d = toHSV('d33b69');
    console.log(d);

    const e = toRGB2([20.104, 0.749, 1]);
    console.log(e);

    const f = tmp.rgb;
    const g = tmp.hsv;
    const h = tmp.complementaryColor;
    console.log(f);
    console.log(g);
    console.log(h);

    return (
        <div className="App">
            <header className="App-header">
                <img src={logo} className="App-logo" alt="logo" />
                <p>Edit src/App.tsx and save to reload.</p>
                <a
                    className="App-link"
                    href="https://reactjs.org"
                    target="_blank"
                    rel="noopener noreferrer"
                >
                    Learn React
                </a>
            </header>
        </div>
    );
}

export default App;
